import React, {
  useState,
  useCallback,
  useEffect,
  useRef,
  Suspense,
} from "react";
import {
  HStack,
  VStack,
  Text,
  useToast,
  Menu,
  Center,
  Hidden,
  useMediaQuery,
  Pressable,
  ChevronRightIcon,
  useBreakpointValue,
  Spinner,
} from "native-base";
import { useParams, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

import { vw, vh } from "../../../util";
import Colors from "../../../constants/Colors";
import * as propertiesActions from "../../../store/actions/properties";
import * as settingsActions from "../../../store/actions/settings";
import * as authActions from "../../../store/actions/auth";
import * as planActions from "../../../store/actions/plan";
import SettingsIcon from "../../../components/svg/SettingsIcon";
import DrivingRoutesIcon from "../../../components/svg/DrivingRoutesIcon";
import TeamIcon from "../../../components/svg/TeamIcon";
import AnalyticsIcon from "../../../components/svg/AnalyticsIcon";
import ChatSupportIcon from "../../../components/svg/ChatSupportIcon";
import FeatureRequestIcon from "../../../components/svg/FeatureRequestIcon";
import ReportIssueIcon from "../../../components/svg/ReportIssueIcon";
import SubscriptionIcon from "../../../components/svg/SubscriptionIcon";
import LogoutIcon from "../../../components/svg/LogoutIcon";
import CreditsIcon from "../../../components/svg/CreditsIcon";

// import ShadowModal from "../../../components/ShadowModal";
// import ShadowButton from "../../../components/ShadowButton";

const ShadowModal = React.lazy(() => import("../../../components/ShadowModal"));
const ShadowButton = React.lazy(() =>
  import("../../../components/ShadowButton")
);
const NavBar = React.lazy(() => import("../home/NavBar"));
const Settings = React.lazy(() => import("./Settings"));
const Analytics = React.lazy(() => import("./Analytics"));
const DriveRoutes = React.lazy(() => import("./DriveRoutes"));
const Features = React.lazy(() => import("./Features"));
const Subscriptions = React.lazy(() => import("./Subscriptions"));
const Issues = React.lazy(() => import("./Issues"));
const Team = React.lazy(() => import("./Team"));
const Chat = React.lazy(() => import("./Chat"));
const Credits = React.lazy(() => import("./Credits"));
const Faq = React.lazy(() => import("./Faq"));

// import Analytics from "./Analytics";
// import DriveRoutes from "./DriveRoutes";
// import Features from "./Features";
// import Subscriptions from "./Subscriptions";
// import Issues from "./Issues";
// import Team from "./Team";
// import Chat from "./Chat";
// import Credits from "./Credits";

// import Faq from "./Faq";

const useMountedState = () => {
  const mountedRef = useRef(false);
  const isMounted = useCallback(() => mountedRef.current, []);

  useEffect(() => {
    mountedRef.current = true;

    return () => {
      mountedRef.current = false;
    };
  }, []);

  return isMounted;
};

const Index = ({ user, setLogout, isSubscribed }) => {
  const mRef = useRef();
  const [landscape] = useMediaQuery([
    {
      orientation: "landscape",
    },
    {
      orientation: "portrait",
    },
  ]);
  const sz = useBreakpointValue({
    base: "base",
    sm: "sm",
    lg: "lg",
    md: "md",
    xl: "xl",
  });

  let { id: tab } = useParams();
  const navigate = useNavigate();
  const toast = useToast();
  const [isHover, setIsHover] = useState(null);
  const [isOpen, setIsOpen] = useState(false);
  const [isOpenLogout, setIsOpenLogout] = useState(false);
  const [isTerminating, setIsTerminating] = useState(false);
  const dispatch = useDispatch();
  const isMounted = useMountedState();
  const { profile } = useSelector((state) => state.auth);
  const tags = useSelector((state) => state.properties.tags);

  useEffect(() => {
    // console.log(isSubscribed(), tab);
    if (!isSubscribed() && tab !== "subscriptions") {
      navigate("/account/subscriptions", { replace: true });
      return;
    }
  }, [tab, isSubscribed, navigate]);

  const _showAlert = useCallback(
    (status, msg) => {
      toast.show({
        placement: "top",
        duration: 2000,
        render: () => {
          let bg = "red.300";
          if (status === "error") {
            bg = "red.300";
          } else if (status === "success") {
            bg = "green.300";
          } else if (status === "info") {
            bg = "blueGray.300";
          } else if (status === "warning") {
            bg = "orange.300";
          }
          return (
            <Text
              style={{
                boxShadow: "0px 3px 10px #0000001A",
                fontSize: vw(12),
                fontFamily: "Poppins_medium",
                color: "#33214B",
                marginHorizontal: vw(10),
                paddingHorizontal: vw(10),
                textAlign: "center",
              }}
              p={2}
              bg={bg}
              rounded={"full"}
            >
              {msg}
            </Text>
          );
        },
      });
    },
    [toast]
  );

  const loadInit = useCallback(async () => {
    try {
      await dispatch(authActions.getProfile());
      await dispatch(planActions.fetchPlans());
      // await dispatch(propertiesActions.fetchStatus());
      await dispatch(propertiesActions.fetchTags());
      await dispatch(authActions.getFaqs());
      // await dispatch(campaignsActions.fetchCampaigns());
      // await dispatch(propertiesActions.fetchPropertyById(id));
    } catch (err) {
      _showAlert("error", "something went wrong.");
    }
  }, [dispatch, _showAlert]);

  useEffect(() => {
    if (isMounted) {
      loadInit().then(() => {});
    }
  }, [dispatch, loadInit, isMounted]);

  const terminateAccount = async () => {
    try {
      setIsTerminating(true);
      const invitecode =
        global.user && global.user.invitecode
          ? global.user.invitecode
          : "owner";
      const action = settingsActions.terminateAccount(invitecode);
      const terminated = await dispatch(action);
      if (terminated) {
        _showAlert(
          "success",
          "Your request for your account's termination is accepted and terminated permanently."
        );
      }
      setIsTerminating(false);
      setLogout(true);
      navigate("/");
    } catch (err) {
      console.log(err.message);
      setIsTerminating(false);
      _showAlert("error", err.message);
    }
  };

  return (
    <VStack style={{ minHeight: "100vh" }} bg={"#F7F8FA"}>
      <NavBar user={user} setLogout={setLogout} position='fixed' />
      <HStack
        justifyContent={["center", "center", "space-between"]}
        style={{
          flex: 1,
          marginTop: vh(45),
          // justifyContent: "space-between",
        }}
      >
        {/* Account Menu */}

        <VStack
          ref={mRef}
          width={[vw(70), vw(70), vw(70), vw(255)]}
          style={{
            // width: vw(255),
            height: vh(500),
            borderRightWidth: vw(1),
            borderRightColor: "rgba(0,0,0, 0.15)",
            left: 0,
            flex: 1,
            marginTop: vh(45),
            position:
              sz === "base" || (sz === "sm" && !landscape) ? "fixed" : "fixed",
            zIndex: 99,
            display: sz === "base" || sz === "sm" || sz === "md" ? "none" : "flex",
          }}
        >
          {user.owner && (
            <Menu.Item
              onHoverIn={() => {
                setIsHover("subscriptions");
              }}
              onHoverOut={() => {
                setIsHover(null);
              }}
              onPress={() => {
                if (sz === "base" || sz === "sm") {
                  mRef.current.style.display = "none";
                }
                navigate("/account/subscriptions");
              }}
              height={vh(50)}
              backgroundColor='#F7F8FA'
            >
              <HStack
                backgroundColor={["#F7F8FA"]}
                justifyContent={"flex-start"}
              >
                <Center style={{ paddingRight: 20 }}>
                  <SubscriptionIcon
                    fill={
                      isHover === "subscriptions" || tab === "subscriptions"
                        ? Colors.primary
                        : "#686868"
                    }
                  />
                </Center>
                <Hidden only={["base", "sm", "md"]}>
                  <Text
                    style={{
                      marginVertical: vh(15),
                      textAlign: "left",
                      fontFamily: "Poppins_medium",
                      color:
                        isHover === "subscriptions" || tab === "subscriptions"
                          ? Colors.primary
                          : "#686868",
                      fontSize: vw(15),
                    }}
                  >
                    Subscriptions
                  </Text>
                </Hidden>
              </HStack>
            </Menu.Item>
          )}
          {isSubscribed() && (
            <>
              {user.owner && (
                <Menu.Item
                  onHoverIn={() => {
                    setIsHover("credits");
                  }}
                  onHoverOut={() => {
                    setIsHover(null);
                  }}
                  onPress={() => {
                    if (sz === "base" || sz === "sm") {
                      mRef.current.style.display = "none";
                    }
                    navigate("/account/credits");
                  }}
                  height={vh(50)}
                  backgroundColor='#F7F8FA'
                >
                  <HStack
                    backgroundColor={"#F7F8FA"}
                    justifyContent={"flex-start"}
                  >
                    <Center style={{ paddingRight: 20 }}>
                      <CreditsIcon
                        fill={
                          isHover === "credits" || tab === "credits"
                            ? Colors.primary
                            : "#686868"
                        }
                      />
                    </Center>
                    <Hidden only={["base", "sm", "md"]}>
                      <Text
                        style={{
                          marginVertical: vh(15),
                          textAlign: "left",
                          fontFamily: "Poppins_medium",
                          color:
                            isHover === "credits" || tab === "credits"
                              ? Colors.primary
                              : "#686868",
                          fontSize: vw(15),
                        }}
                      >
                        Credits
                      </Text>
                    </Hidden>
                  </HStack>
                </Menu.Item>
              )}
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("settings");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/settings");
                }}
                backgroundColor='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <SettingsIcon
                      fill={
                        isHover === "settings" || tab === "settings"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "settings" || tab === "settings"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Settings
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("drive");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/drive");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <DrivingRoutesIcon
                      fill={
                        isHover === "drive" || tab === "drive"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "drive" || tab === "drive"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Driving Routes
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              {user.owner &&
                profile &&
                profile.plan &&
                profile.plan.type === "team" && (
                  <Menu.Item
                    onHoverIn={() => {
                      setIsHover("team");
                    }}
                    onHoverOut={() => {
                      setIsHover(null);
                    }}
                    onPress={() => {
                      if (sz === "base" || sz === "sm") {
                        mRef.current.style.display = "none";
                      }
                      navigate("/account/team");
                    }}
                    background='#F7F8FA'
                    height={vh(50)}
                  >
                    <HStack justifyContent={"flex-start"}>
                      <Center style={{ paddingRight: 20 }}>
                        <TeamIcon
                          fill={
                            isHover === "team" || tab === "team"
                              ? Colors.primary
                              : "#686868"
                          }
                        />
                      </Center>
                      <Hidden only={["base", "sm", "md"]}>
                        <Text
                          style={{
                            marginVertical: vh(15),
                            textAlign: "left",
                            fontFamily: "Poppins_medium",
                            color:
                              isHover === "team" || tab === "team"
                                ? Colors.primary
                                : "#686868",
                            fontSize: vw(15),
                          }}
                        >
                          Team
                        </Text>
                      </Hidden>
                    </HStack>
                  </Menu.Item>
                )}
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("analytics");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/analytics");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <AnalyticsIcon
                      fill={
                        isHover === "analytics" || tab === "analytics"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "analytics" || tab === "analytics"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Analytics
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              {/* {user.plan === "team" && ( */}
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("chat");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/chat");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <ChatSupportIcon
                      fill={
                        isHover === "chat" || tab === "chat"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "chat" || tab === "chat"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Chat Support
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              {/* )} */}
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("feature");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/feature");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <FeatureRequestIcon
                      fill={
                        isHover === "feature" || tab === "feature"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "feature" || tab === "feature"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Feature Request
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("issue");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/issue");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <ReportIssueIcon
                      fill={
                        isHover === "issue" || tab === "issue"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "issue" || tab === "issue"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      Report an Issue
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
              <Menu.Item
                onHoverIn={() => {
                  setIsHover("faq");
                }}
                onHoverOut={() => {
                  setIsHover(null);
                }}
                onPress={() => {
                  if (sz === "base" || sz === "sm") {
                    mRef.current.style.display = "none";
                  }
                  navigate("/account/faq");
                }}
                background='#F7F8FA'
                height={vh(50)}
              >
                <HStack justifyContent={"flex-start"}>
                  <Center style={{ paddingRight: 20 }}>
                    <ReportIssueIcon
                      fill={
                        isHover === "faq" || tab === "faq"
                          ? Colors.primary
                          : "#686868"
                      }
                    />
                  </Center>
                  <Hidden only={["base", "sm", "md"]}>
                    <Text
                      style={{
                        marginVertical: vh(15),
                        textAlign: "left",
                        fontFamily: "Poppins_medium",
                        color:
                          isHover === "faq" || tab === "faq"
                            ? Colors.primary
                            : "#686868",
                        fontSize: vw(15),
                      }}
                    >
                      FAQ
                    </Text>
                  </Hidden>
                </HStack>
              </Menu.Item>
            </>
          )}
          <Menu.Item
            onHoverIn={() => {
              setIsHover("logout");
            }}
            onHoverOut={() => {
              setIsHover(null);
            }}
            background='#F7F8FA'
            height={vh(50)}
            onPress={async () => {
              setIsOpenLogout(true);
            }}
          >
            <HStack justifyContent={"flex-start"}>
              <Center style={{ paddingRight: 20 }}>
                <LogoutIcon
                  fill={isHover === "logout" ? Colors.primary : "#686868"}
                />
              </Center>
              <Hidden only={["base", "sm", "md"]}>
                <Text
                  style={{
                    marginVertical: vh(15),
                    textAlign: "left",
                    fontFamily: "Poppins_medium",
                    color: isHover === "logout" ? Colors.primary : "#686868",
                    fontSize: vw(15),
                  }}
                >
                  Logout
                </Text>
              </Hidden>
            </HStack>
          </Menu.Item>
          {/* <Menu.Item
            onHoverIn={() => {
              setIsHover("terminate");
            }}
            onHoverOut={() => {
              setIsHover(null);
            }}
            background='#F7F8FA'
            height={vh(50)}
            onPress={async () => {
              setIsOpen(true);
            }}
            style={{ marginTop: vh(20) }}
          >
            <HStack justifyContent={"flex-start"}>
              <Center style={{ paddingRight: 20 }}>
                <CloseIcon
                  fill={isHover === "terminate" ? Colors.primary : "#686868"}
                />
              </Center>
              <Text
                style={{
                  marginVertical: vh(15),
                  textAlign: "left",
                  fontFamily: "Poppins_medium",
                  color: isHover === "terminate" ? Colors.primary : "#686868",
                  fontSize: vw(15),
                }}
              >
                Terminate
              </Text>
            </HStack>
          </Menu.Item> */}
        </VStack>
        <Hidden from='lg'>
          <Pressable
            onPress={() => {
              if (mRef.current) {
                mRef.current.style.display =
                  mRef.current.style.display === "flex" ? "none" : "flex";
              }
            }}
            style={{
              backgroundColor: "rgba(0,0,0,0.3)",
              width: 18,
              height: 80,
              position: "fixed",
              alignSelf: "flex-start",
              top: "50%",
              left: 0,
              zIndex: 999,
            }}
          >
            <Center flex={1}>
              <ChevronRightIcon color='white' />
            </Center>
          </Pressable>
        </Hidden>
        {/* Loading area */}
        <VStack
          marginLeft={[vw(0), vw(0), vw(0), vw(255)]}
          paddingX={[vw(20), vw(20), vw(20), vw(40)]}
          style={{
            flex: 1,
            // paddingHorizontal: vw(40),
            backgroundColor: "#F7F8FA",
            // marginLeft: vw(255),
            marginTop: vh(45),
          }}
        >
          {tab === "subscriptions" && profile && (
            <>
              {user.owner ? (
                <Subscriptions
                  landscape={landscape}
                  profile={profile}
                  _showAlert={_showAlert}
                />
              ) : (
                <Center flex={1}>
                  <Text
                    style={{
                      fontFamily: "Poppins_medium",
                      fontSize: vw(15),
                      color: "rgba(0,0,0,0.5)",
                    }}
                  >
                    Contact your team owner to renew your subscription and then
                    restart your app. Thank you.
                  </Text>
                </Center>
              )}
            </>
          )}
          {tab === "credits" && profile && (
            // <Elements stripe={stripePromise}>
            <Suspense>
              <Credits profile={profile} _showAlert={_showAlert} />
            </Suspense>

            // </Elements>
          )}
          {tab === "settings" && profile && (
            <Suspense>
              <Settings tags={tags} profile={profile} _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "drive" && (
            <Suspense>
              <DriveRoutes />
            </Suspense>
          )}
          {tab === "team" && profile._id === profile.team && (
            <Suspense>
              <Team profile={profile} _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "analytics" && profile && (
            <Suspense>
              <Analytics profile={profile} _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "chat" && profile && (
            <Suspense>
              <Chat profile={profile} _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "feature" && profile && (
            <Suspense>
              <Features _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "issue" && (
            <Suspense>
              <Issues _showAlert={_showAlert} />
            </Suspense>
          )}
          {tab === "faq" && (
            <Suspense>
              <Faq _showAlert={_showAlert} />
            </Suspense>
          )}
        </VStack>
      </HStack>

      <ShadowModal
        placement='top'
        size={"md"}
        isOpen={isOpenLogout}
        close={() => {
          setIsOpenLogout(false);
        }}
        header='Logout'
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={4}
              onPress={() => {
                setIsOpenLogout(false);
              }}
              variant='outline'
              label='No'
              style={{
                height: vh(36),
              }}
            />
            <ShadowButton
              space={3}
              onPress={async () => {
                setLogout(true);
                mRef.current.style.display = "none";
                navigate("/");
              }}
              color='white'
              label='Yes'
              style={{
                height: vh(36),
              }}
            />
          </HStack>
        }
      >
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(15),
            color: "rgba(0,0,0,0.8)",
          }}
        >
          Are you sure you want to logout?
        </Text>
      </ShadowModal>

      <ShadowModal
        style={{ top: -(document.body.scrollHeight / 2) + 300 }}
        size={"md"}
        isOpen={isOpen}
        close={() => {
          setIsOpen(false);
        }}
        header='Terminate Account'
        buttons={
          <>
            <ShadowButton
              onPress={() => {
                setIsOpen(false);
              }}
              color='rgba(0,0,0, 0.5)'
              variant='outline'
              label='Cancel'
              style={{
                height: vh(36),
              }}
            />
            <ShadowButton
              isLoading={isTerminating}
              onPress={async () => {
                await terminateAccount();
              }}
              color='white'
              label='Terminate'
              style={{
                height: vh(36),
              }}
            />
          </>
        }
      >
        <Text style={{ textAlign: "left", marginBottom: 10, fontSize: 16 }}>
          Your account will be terminated permanently and all your data will be
          erased and cannot be recovered here after.
        </Text>
        <Text style={{ textAlign: "left", marginBottom: 20, fontSize: 16 }}>
          If you like to terminate your account now, please press the terminate
          button below or contact us at support@shadow.properties if you'd like
          to discuss your account.
        </Text>
        <Text
          style={{
            fontFamily: "Poppins_medium",
            fontSize: vw(15),
            color: "rgba(0,0,0,0.8)",
          }}
        >
          Are you sure you want to terminate?
        </Text>
      </ShadowModal>
    </VStack>
  );
};

export default Index;
