import { API_URL, getUser, setUser } from "../../constants/Config";
import * as events from "../events";

export const updateSubscription = ({ subid, planid, freecredits, pi, profile }) => {
  return async (dispatch, getState) => {
    try {
      const { token } = getUser();
      const response = await fetch(`${API_URL}/users/${profile._id}`, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({
          upgraded: profile.subscribed ? true : false,
          subscribed: true,
          subid,
          email: profile.email,
          planid,
          freecredits,
          pi,
          balancecredits: profile.credits,
        }),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();
      let success = false;
      if (resData.data) {
        success = resData.updated;
        global.subscribed = true;
        // console.log("updateSubscription global.subscribed", global.subscribed);
        // dispatch({
        //   type: events.SET_FTNOTICE,
        //   data: ,
        // });
        // dispatch({
        //   type: events.SET_PROFILE,
        //   data: resData.data,
        // });
      }

      return success;
    } catch (err) {
      // send to custom analytics server
      console.log("Subscription Error Here  ", err);
      throw err;
    }
  };
};

export const fetchPlans = (page = 1, limit = 10) => {
  return async (dispatch, getState) => {
    // any async code you want!
    const { token } = getUser();
    const body = JSON.stringify({
      skip: page === 1 ? 0 : (page - 1) * limit,
      limit,
    });
    try {
      const response = await fetch(`${API_URL}/plans/get`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body,
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();

      dispatch({
        type: events.SET_PLANS,
        data: resData.data ? resData.data : [],
      });
    } catch (err) {
      // send to custom analytics server
      console.log("Error ", err);
      throw err;
    }
  };
};

