export const delPM = (id) => {
  return async (dispatch, getState) => {
    const { token } = getUser();

    // console.log(id);
    try {
      const response = await fetch(`${API_URL}/users/pm/del`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ id }),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();
      return resData.data ? resData.data : null;
    } catch (err) {
      // send to custom analytics server
      console.log("Error ", err);
      throw err;
    }
  };
};

export const setAsDefaultPM = (id) => {
  return async (dispatch, getState) => {
    const { token } = getUser();

    try {
      const response = await fetch(`${API_URL}/users/pm/default`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: "Bearer " + token,
        },
        body: JSON.stringify({ id }),
      });

      if (!response.ok) {
        console.log(await response.json());
        throw new Error("Something went wrong!");
      }

      const resData = await response.json();
      setUser("pm", id);

      return resData.data ? resData.data : null;
    } catch (err) {
      // send to custom analytics server
      console.log("Error ", err);
      throw err;
    }
  };
};

